import { createWebHistory, createRouter } from "vue-router";

import HomeDzny from "./components/HomeDzny";
import BoardDzny from "./components/BoardDzny";
import SearchDzny from "./components/SearchDzny";
import DownloadDzny from "./components/DownloadDzny";
import AccountDzny from "./components/AccountDzny";

const routes = [
  {
    path: "/",
    component: HomeDzny
  },
  {
    path: "/Board",
    component: BoardDzny
  },
  {
    path: "/Search",
    component: SearchDzny
  },
  {
    path: "/Download",
    component: DownloadDzny
  },
  {
    path: "/Account",
    component: AccountDzny
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;
